<template>
  <div>
    <div hoverable style="border: 1px solid rgba(0,0,0,0.1); padding-left: 15px; padding-bottom: 10px">
      <div class="mr-t">标签</div>
      <a-divider class="mr-t" style="margin-top: 10px; margin-bottom: 5px; margin-left: -7px"/>
      <div class="mr-t">
        <a-tag style="margin: 5px" v-for="i in tags" :key="i.id"><a :href="'/tags/' + i.name">{{i.name}}</a></a-tag>
      </div>
    </div>
  </div>
</template>

<script>
  import TagsApi from "../../../api/Tags";
  export default {
    name: "Tag",
    data() {
      return {
        tags: []
      }
    },
    created () {
      this.loadCates()
    },
    methods: {
      async loadCates() {
        TagsApi.loadTags().then(res => {
          if (res.code === 1000) {
            this.tags = res.data.tags
          } else {
            this.$message.error('加载分类列表失败')
          }
        })
      }
    }
  }
</script>

