<template>
  <div>
    <div style="border: 1px solid rgba(0,0,0,0.1); padding-left: 15px">
      <div class="mr-t">分类</div>
      <a-divider class="mr-t" style="margin-top: 10px; margin-bottom: 5px; margin-left: -7px"/>
      <a-list size="small" :data-source="categories">
        <a-list-item slot="renderItem" slot-scope="item">
          <div style="float:left;"><router-link class="link-black" :to="'/categories/' + item.name">{{ item.name }}</router-link>
          </div>
          <div style="float: right;margin-right: 10px">
            <a-tag color="orange">
              {{item.count}}
            </a-tag>
          </div>
        </a-list-item>
      </a-list>
    </div>
  </div>
</template>

<script>
  import CategoryApi from "../../../api/Categories";
  export default {
    name: "Category",
    data() {
      return {
        categories: []
      }
    },
    created () {
      this.loadCates()
    },
    methods: {
      async loadCates() {
        CategoryApi.loadCategories().then(res => {
          if (res.code === 1000) {
            this.categories = res.data.categories
          } else {
            this.$message.error('加载分类列表失败')
          }
        })
      }
    }
  }
</script>

