<template>
  <div>
    <a-card hoverable>
      <img
        slot="cover"
        alt="example"
        :height="150"
        src="https://api.ixiaowai.cn/gqapi/gqapi.php"
      />
      <a-card-meta :title="admin.nickname?admin.nickname:'xxxxx'" :description="admin.personalNote?admin.personalNote:'xxxx'">
        <a-avatar
          slot="avatar"
          :src="admin.avatar?admin.avatar:'https://cdn.v2ex.com/gravatar?d=wavatar'"
        />
      </a-card-meta>
    </a-card>
  </div>
</template>

<script>
  import AdminApi from "../../../api/Admin";
  export default {
    name: "Profile",
    data() {
      return {
        admin: {}
      }
    },
    created () {
      this.loadAdminInfo()
    },
    methods: {
      async loadAdminInfo() {
        AdminApi.getAdminInfo().then(res => {
          if (res.code === 1000) {
            this.admin = res.data.admin
          } else {
            this.$message.error('获取用户信息失败')
          }
        })
      }
    }
  }
</script>

