<script>
  export default {
    name: "NarBar"
  }
</script>

<style lang="less" scoped>
  body {
    padding: 0;
    margin: 0;
    .sider-menu {
      float: right;
      display: none;
    }
    .top-menu {
      display: flex;
      justify-content: space-between;
    }
    .top-nav-items {
      ul {
        display: flex;
        justify-content: space-around;
        li {
          list-style: none;
          transition: .5s;
          *:last-child {
            margin-top: 2px;
            line-height: 44px;
            padding: 0 15px;
          }
          a {
            text-align: center;
            text-decoration: none;
            line-height: 44px;
            padding: 0 15px;
            color: #000;
            transition: .5s;
            cursor: pointer;
            position: relative;
          }
          a:before {
            content: "";
            position: absolute;
            bottom: 0;
            left: 16px;
            width: 8px;
            height: 8px;
            border: 2px solid #e91e63;
            border-width: 0 0 2px 2px;
            transition: .5s;
            opacity: 0;
          }
          a:after {
            content: "";
            position: absolute;
            top: 0;
            right: 16px;
            width: 8px;
            height: 8px;
            border: 2px solid #e91e63;
            border-width: 2px 2px 0 0;
            transition: .5s;
            opacity: 0;
          }
          a:hover:before {
            bottom: -4px;
            left: -4px;
            opacity: 1;
          }
          a:hover:after {
            top: -4px;
            right: -4px;
            opacity: 1;
          }
          a:hover {
            color: #fff;
            background: #e91e63;
          }
        }
      }
    }
    .logo {
      line-height: 44px;
      text-transform: uppercase;
      color: #000;
      cursor: pointer;
    }
    header {
      background-color: rgb(253,253,253);
      height: 44px;
      backdrop-filter: blur(20px);
      .sider-menu #toggler {
        display: none;
        &:checked + label .hamburger-container span:nth-child(1) {
          transform: rotate(45deg);
          top: 5px;
          transition: top 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
        }
        &:checked + label .hamburger-container span:nth-child(2) {
          transform: rotate(-45deg);
          top: -4px;
          transition: top 0.3s ease-in-out, transform 0.3s ease-in-out 0.3s;
        }
        &:checked ~ .nav-items {
          transform: scaleY(1);
          opacity: 1;
        }
      }
      .sider-menu .hamburger-container {
        display: block;
        width: 26px;
        height: 26px;
        position: relative;
        top: 16px;
        span {
          display: block;
          height: 1px;
          background-color: #000;
          position: relative;
          /*   transition:all .3s ease-in-out; */
          transition: transform 0.3s ease-in-out, top 0.3s ease-in-out 0.3s;
          top: 0;
          &:nth-child(2) {
            margin-top: 8px;
          }
        }
      }
    }
    .nav-items {
      width: 100%;
      height: calc(100vh - 44px);
      background: #fff;
      position: absolute;
      top: 44px;
      left: 0;
      transform: scaleY(0);
      transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out;
      transform-origin: 50% 0;
      opacity: 0;
      margin-left: 0!important;
      ul {
        margin-top: 1em;
        padding-left: 0!important;
        li {
          list-style: none;
          text-align: center;
          padding: 15px 0;
          a {
            font-size: 20px;
            color: #000;
          }
        }
      }
    }
  }

  @media screen and (max-width: 800px) {
    .top-nav-items {
      display: none;
    }
    .shadow {
      position: fixed;
      z-index: 999999;
      width: 100%;
      top: 0;
    }
    .containers {
      width: 90%;
      display: flex;
      justify-content: space-between!important;;
    }
    .sider-menu {
      display: block !important;
      float: right;
    }
  }
</style>