<template>
  <div>
    <NarBar/>
    <div class="containers mr-tt">
      <a-row class="items" :gutter="24">
        <a-col :span="16">
          <a-card class="bread">
            <a-breadcrumb>
              <a-breadcrumb-item><a href="/">首页</a></a-breadcrumb-item>
              <a-breadcrumb-item>随笔</a-breadcrumb-item>
            </a-breadcrumb>
          </a-card>
          <a-card>
            <a-list :loading="loading" item-layout="vertical" size="large" :pagination="pagination" :data-source="essays">
              <a-list-item slot="renderItem" key="item.title" slot-scope="item">
                <a-list-item-meta >
                  <a slot="title">{{ item.time }}</a>
                  <a-avatar slot="avatar" :src="avatar" />
                </a-list-item-meta>
                {{ item.contents }}
              </a-list-item>
            </a-list>
          </a-card>
        </a-col>
        <a-col class="right-items" :span="8">
          <Profile/>
          <Category/>
          <Tag/>
        </a-col>
      </a-row>
    </div>
  </div>
</template>

<script>
  import NarBar from "./components/NarBar";
  import Profile from "./components/Profile";
  import Category from "./components/Category";
  import Tag from './components/Tag'
  import EssaysApi from "../../api/Essays";
  export default {
    name: "Essays",
    components: {
      NarBar,
      Profile,
      Category,
      Tag
    },
    data() {
      return {
        essays: [],
        avatar: '',
        pagination: {
          onChange: page => {
            console.log(page);
          },
          pageSize: 7,
        },
        loading: true
      }
    },
    created () {
      this.loadEssays()
    },
    methods: {
      async loadEssays () {
        EssaysApi.All().then(res => {
          if (res.code === 1000) {
            this.essays = res.data.essays
            this.avatar = res.data.avatar
            this.loading = false
          }
        }).catch(err => {
          this.$message.error(err.toString())
        })
      },
    }
  }
</script>

<style lang="less" scoped>
  .right-items > *:not(:first-child) {
    margin-top: 10px;
  }
  @media screen and (max-width: 800px) {
    .containers {
      width: 95%;
      padding-right: 0 !important;
      margin-top: 40px;
    }
    .items > * {
      padding-left: 12px;
      padding-right: 12px;
      width: 100%;
    }
    .items > *:last-child {
      margin-top: 10px;
    }
  }
</style>